@import url(https://cdnjs.cloudflare.com/ajax/libs/weather-icons/2.0.9/css/weather-icons.min.css);
html,
body,
.background {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Quicksand", sans-serif !important;
}
#root {
  height: 100%;
  background: #fff;
}
img {
  max-width: 100%;
  height: auto;
}

.weather-panel {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid rgba(255, 255, 255, 0.3);
  padding-bottom: 0.25rem;
}
.weather-panel .weather-panel-left .select-date > div {
  padding-left: 0;
  background-color: transparent;
  border: none;
  color: #fff;
}

.weather-panel .weather-panel-left .select-date > span > span {
  opacity: 0;
}

.weather-panel .weather-panel-right {
  display: flex;
}
.weather-panel .weather-panel-right > div {
  margin-left: 0.75rem;
}

.weather-panel-tab {
  display: flex;
}

.weather-panel-tab,
.weather-panel-tab li {
  padding: 0;
  margin: 0;
  list-style: none;
  outline: none;
  opacity: 0.7;
  color: #fff;
  font-size: 16px;
}
.weather-panel-tab li[class*="--selected"] {
  opacity: 1;
  transform: scale(1.1);
  display: flex;
}
.weather-panel-tab li {
  margin-left: 0.675rem;
}

.temp-panel {
  display: flex;
  justify-content: space-between;
}
.temp-panel .temp-item {
  padding-top: 0.5rem;
  font-size: 0.75rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1;
}
.temp-panel .temp-item img {
  width: 26px;
  margin-bottom: 0.125rem;
}

.temp-panel .temp-item p {
  margin-bottom: 0;
}

/* @import "https://fonts.googleapis.com/css?family=Lato:300"; */

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-box-pack: center;
  margin: 0;
  padding: 1rem;
  height: 100%;
  width: 100%;
  overflow: auto;
  position: relative;
}

nav ul {
  margin: 20px 20px 0 20px;
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: row;
}
nav li a {
  display: block;
  width: 50px;
  text-align: center;
  color: #aaa;
  cursor: pointer;
}
nav li a:hover {
  color: #444;
}
nav li a.active {
  color: #4444ff;
}

#card {
  box-shadow: 9px 7px 40px -6px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  width: 300px;
  padding: 0;
  height: 450px;
  min-height: 300px;
  margin: 20px;
  border-radius: 5px;
  position: relative;
}
#card .details {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 16px 20px;
  color: #888;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  transition: color 2s ease;
}
.thunder #card .details {
  color: #ddd;
}
#card .details .right {
  text-align: right;
}
#card .details #date {
  margin: 4px 0;
}
#card .details #summary {
  font-weight: 500;
  font-size: 17px;
}
#card .details .temp {
  font-size: 60px;
  line-height: 60px;
}

#card .details .left {
  display: flex;
  align-items: center;
}

#card .bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 1rem;
  color: #fff;
}

#card .details .icon img {
  width: 60px;
  height: auto;
}
#card .details .temp span {
  font-size: 40px;
  line-height: 30px;
  vertical-align: top;
  margin-left: 5px;
  position: absolute;
}

.weather {
  background-color: #dae3fd;
  transition: background-color 2s ease;
}
.thunder .weather {
  background-color: #9fa4ad;
}
.rain .weather {
  background-color: #d8d8d8;
}
.sun .weather {
  background-color: #ccccff;
}
.rain .weather #inner,.thunder .weather #inner  {
  background: #aaaeb9;
  background-image: linear-gradient(
    -180deg,
    rgba(207, 217, 223, 1) 0%,
    rgba(226, 235, 240, 0.1) 100%
  );
  background-blend-mode: lighten;
}
.snow .weather #inner {
  background-image: linear-gradient(
    135deg,
    rgba(131, 168, 195, 1) 31%,
    rgba(107, 190, 203, 1) 77%
  );
}
.weather #inner {
  background-color: white;
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.5) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  background: linear-gradient(
    135deg,
    rgba(206, 221, 196, 1) 31%,
    rgba(107, 190, 203, 1) 77%
  );
}

.clouds .weather #inner {
  background: rgba(206, 221, 196, 1);
  background: linear-gradient(
    135deg,
    rgba(206, 221, 196, 1) 31%,
    rgba(107, 190, 203, 1) 77%
  );
}

.sun .weather #inner {
  background: rgba(153, 229, 255, 1);
  background: radial-gradient(
    circle,
    rgba(206, 221, 196, 1) 0%,
    rgba(103, 217, 255, 1) 56%
  );
}

.weather .cloud {
  transition: fill 2s ease;
}
.weather #cloud1 {
  fill: #efefef;
}
.weather #cloud2 {
  fill: #e6e6e6;
}
.weather #cloud3 {
  fill: #d5d5d5;
}
.clouds .weather #cloud1 {
  fill: #53789e;
}
.clouds .weather #cloud2 {
  fill: #92bad2;
}
.clouds .weather #cloud3 {
  fill: #c5e2f7;
}

.rain .weather #cloud1 {
  fill: #395877;
}
.rain .weather #cloud2 {
  fill: #53789e;
}
.rain .weather #cloud3 {
  fill: #92bad2;
}

.clouds #card .details,
.sun #card .details,
.rain #card .details {
  color: #fff;
}
.thunder .weather #cloud1 {
  fill: #9fa4ad;
}
.thunder .weather #cloud2 {
  fill: #8b8e98;
}
.thunder .weather #cloud3 {
  fill: #7b7988;
}

#outer,
#back {
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

